import React, { useState } from 'react';
import style from '../FAQ/FAQ.module.css';
import SearchInput from '../../../../reusable_components/SearchInput/SearchInput';
import Accordion from './Accordion/Accordion';
import NavLinks from '../../../../reusable_components/NavLinks/NavLinks';

const FAQ = (props) => {
  const [active, setActive] = useState(true);

  return (
    <section className={style.faq}>
      <NavLinks
        data={[
          { url: 'home', name: '', active: false },
          { url: 'about', name: ' ', active: false },
          { url: 'FAQ', name: 'F.A.Q.', active: true },
        ]}
      />
      <div className={style.faq_container}>
        <h1 className={style.faq_title}>F.A.Q.</h1>

        {/* <SearchInput /> */}

        <section class={style.faq_info}>
          <Accordion
            active={active}
            setActive={setActive}
            title={'    H5  ?'}
            subtitle={
              ',      H5  ,  .     H5    95%,      ,       .  H5      .'
            }
          />
          <Accordion
            active={active}
            setActive={setActive}
            title={'  ?'}
            subtitle={
              '. ,          .'
            }
          />
          <Accordion
            active={active}
            setActive={setActive}
            title={'    H5?'}
            subtitle={
              ' H5     . H5   ,         ,      ,       .'
            }
          />
          <Accordion
            active={active}
            setActive={setActive}
            title={'    H5?'}
            subtitle={`     - `}
            href={true}
          />
          <Accordion
            active={active}
            setActive={setActive}
            title={'    H5?'}
            subtitle={
              '       . '
            }
          />
          <Accordion
            active={active}
            setActive={setActive}
            title={' ,     ?'}
            subtitle={
              ' ,            .    -     ,    ,          sale@hi5russia.com,       .'
            }
          />
        </section>
      </div>
    </section>
  );
};

export default FAQ;
